import { Component, OnInit } from '@angular/core';
import { MeetingRoomsService } from '../../Shared/meeting-rooms.service';
import { MeetingRoom } from '../../Shared/meeting-room.model';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-meeting-room-list',
  templateUrl: './meeting-room-list.component.html',
  styles: []
})
export class MeetingRoomListComponent implements OnInit {

  constructor(public service: MeetingRoomsService, 
    private toastr: ToastrService) 
  { }

  ngOnInit() {
    this.service.getList();    

  }
  populateForm(mr:MeetingRoom){   
      this.service.formData = Object.assign({}, mr);          
  }
  onInsert(){
    this.service.formData={
      ID:0, 
      Name:"",
      Size:-1
    };
  }
  onDelete(mr:MeetingRoom){
    if(confirm(`確定刪除 會議室:${mr.Name} 資料卡?`))
    {
      this.service.deleteMeetingRoom(mr.ID).subscribe(
        resp=> {    
          this.toastr.success('刪除成功', '會議室資料卡')
          this.service.getList();
        }, err =>{
      
          console.log(err);
        }
      );
    }
  }
}
