import { Injectable } from '@angular/core';
import { MeetingRoom } from './meeting-room.model';
import { HttpClient  } from '@angular/common/http';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class MeetingRoomsService {
  formData: MeetingRoom;
  //readonly rootUrl='https://localhost:44332/api/meetingrooms'
  rootUrl='';
  list: MeetingRoom[];
  constructor(public http:HttpClient) { 
    this.rootUrl= environment.production?'http://localhost/mywebapi/api/MeetingRooms'
                                        :'http://localhost:44332/api/MeetingRooms:';
  }
  getList(){   

    this.http.get<MeetingRoom[]>(this.rootUrl  )
      .toPromise()
      .then(resp=>this.list=resp as MeetingRoom[]);
    
  }
  getByID(id){
    this.http.get<MeetingRoom>(this.rootUrl+`/${id}`)
    .toPromise()
    .then(resp=>this.formData = resp as MeetingRoom);}
    
  postMeetingRoom(){
    return this.http.post(this.rootUrl , this.formData);
  }
  putMeetingRoom() {
    return this.http.put(this.rootUrl + '/'+ this.formData.ID, this.formData);
  }
  deleteMeetingRoom(id) {
    return this.http.delete(this.rootUrl + '/'+ id);
  }
}
